function initSiteContents() {
  document.getElementById('site-icon').setAttribute('href', site_icon_path);
  document.getElementById('site-logo').setAttribute('src', site_logo_path);
  document.getElementById('site-title').setAttribute('data', site_title);

  document.getElementById('code-for-sketch-icon').setAttribute('data', code_for_sketch_icon_text);
  document.getElementById('code-for-sketch-text').setAttribute('data', code_for_sketch_text);

  document.getElementById('instagram-icon').setAttribute('src', instagram_icon_src);
  document.getElementById('instagram-text').setAttribute('data', instagram_text);

  document.getElementById('github-icon').setAttribute('src', github_icon_light_src);
  document.getElementById('github-text').setAttribute('data', github_text);

  document.getElementById('copyright-icon').setAttribute('text', copyright_symbol);
  document.getElementById('copyright-text').setAttribute('data', copyright_text);
}

initSiteContents();
readCsv();

let sketchUrls = [];
let currentSketchIndex = 0;

// CSV data with sketch descriptions
// import { readFile } from 'fs';

let csvData = '';

async function readCsv() {
  let response = await fetch('sketchData.csv');
  csvData = await response.text();
  
  const csvLines = csvData.split('\n');
  const headerRow = csvLines[0].split(',');
  const csvDescriptions = {};
  for (let i = 1; i < csvLines.length; i++) {
      const fields = csvLines[i].split(',');
      if (fields[0].length < 4) {
        break;
      }
      if (fields.length === headerRow.length) {
          csvDescriptions[fields[0].split('/').pop()] = {
              desc: fields[1] || 'Description not available',
              title: fields[2] || '',
              subtitle: fields[3] || ''
          };
      }
      if (fields.length > 0) {
          sketchUrls.push(fields[0].trim());
      }
  }
  
  currentSketchIndex = Math.floor(Math.random() * sketchUrls.length);

  if (sketchUrls.length > 0) {
    updateSketchFrame();
  }
  console.log(sketchUrls.length);
}

